html{
  box-sizing:border-box;
  background-color: #eeee;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Montserrat';
}


*, ::after, ::before {
  box-sizing: inherit;
}

.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

.form__input {
    padding: 10px;
    border: 1px solid #ddd;
}

.form__button {
    border: none;
    background-color:hsl(180, 100%, 25%);
    color: white;
    padding: 10px;
    transition: 1s;
    cursor: pointer;
}

.form__button:hover {
    background: hsl(180, 100%, 35%);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.form__button:active {
    background: hsl(180, 100%, 35%);
}

@media (max-width: 767px) {
    .form__input {
        display: grid;
        grid-template-columns: 1fr;
    }
}
.tasks {
    list-style: none;
    margin: 0;
    padding: 0px;
    word-break: break-word;
}

.tasks__button {
    border: none;
    color: white;
    width: 40px;
    height: 40px;
    padding: 0;
    transition: 1s;
    cursor: pointer;
}

.tasks__button--toggleDone {
    background: hsl(120, 100%, 25%);
}

.tasks__button--toggleDone:hover {
    background: hsl(120, 100%, 35%);
}

.tasks__button--remove {
    background: hsl(0, 100%, 50%);
}

.tasks__button--remove:hover {
    background: hsl(0, 100%, 60%);
}

.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    padding: 10px;
    align-items: center;
    border-bottom: 1px solid #ddd
}

.tasks__item--hidden {
    display: none;
}
.tasks__content--done {
    text-decoration: line-through;
}
.buttons {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
 }

.buttons__button {
    background-color: white;
    border: none;
    color:teal;
    width: 200px;
    height: 50px ;
    transition: 1s;
    padding: 1%;
    cursor: pointer;
}

.buttons__button:disabled { 
    color: #ccc;
}

.buttons__button:hover {
    color: hsl(180, 100%, 35%);
}

@media(max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}

.section{
    background-color: white;   
    margin: 10px 0;
    box-shadow: 2px #0000;
    display: block;
    
}
.section {
    background: white;
    margin: 10px 0;
    box-shadow: 0 0 5px #ddd;
}

.section__header {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: auto auto;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
}

@media(max-width: 767px) {
    .section__header {
        grid-template-columns: 1fr;
    }
}

.section__title {
    font-size: 20px;
    padding: 20px;
    margin: 0;
}

.section__list {
    padding: 0px 20px 20px 20px;
}

.section__body {
    padding: 20px;
}

.container{
    max-width: 950px;
    padding: 30px;
    margin: 0 auto;
}

    

 
